@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap");

@use "sass:math" {}

@mixin for-center-line() {
    display: flex;
    justify-content: center;
    align-items: center; }

@mixin flex-between() {
    display: flex;
    justify-content: space-between; }

//@include flex-between

$breakpoint-mobile: 335px;
$breakpoint-tablet: 758px;
$breakpoint-desktop: 1024px;

$point-color: #A0FCBA;
$white: #fff;
$black: #000;
$main-color: #8CF4E2;
//@include for-center-line
* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box; }
.app {
    width: 100%;
    height: 100vh;
    font-family: "Noto Sans KR", sans-serif;
    button {
        position: fixed;
        bottom: 5px;
        right: 5px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $point-color;
        color: $black;
        font-size: 2em;
        border: none;
        cursor: pointer; } }
nav {
    width: 100%;
    margin-top: 15px;
    ul {
        display: flex;
        justify-content: flex-start;
        margin-left: 15px;
        transition: 0.3s;
        li {
            list-style: none;
            width: 180px;
            padding: 15px 45px;
            margin-right: 20px;
            color: $black;
            font-size: 1.2em;
            letter-spacing: -0.7px;
            background-color: $point-color;
            border-top-left-radius: 5px;
            transition: 0.3s; }

        li::before {
            content: "";
            position: absolute;
            transform: translate(120px , -24px) rotate(-25deg);
            background-color: $white;
            width: 25px;
            height: 65px;
            border-bottom-right-radius: 40px; }
        a {
            text-decoration: none; }

        li:hover {
            background-color: $main-color; }
        .active {
            background-color: $main-color; } } }

@mixin mobile {
    @media (min-width: #{$breakpoint-mobile}) and (max-width: #{$breakpoint-tablet - 1px}) {
        @content; } }

@mixin tablet {
    @media (min-width: #{$breakpoint-tablet}) and (max-width: #{$breakpoint-desktop - 1px}) {
        @content; } }

@include mobile {
    nav {
        margin: 0;
        ul {
            position: fixed;
            right: 0;
            bottom: 0;
            width: 100%;
            li {
                width: 120px;
                margin: 0;
                border-top-left-radius: 0px;
                border-right: 1px solid $main-color;
                border-bottom: 1px solid $main-color;
                padding: 30px 10px 4px 2px; }
            li::before {
                display: none; } } }
    .container-div {
        width: 335px;
        height: fit-content; }
    .container-div::after {
        display: none; }
    .home-div {
        flex-direction: column-reverse;
        border: 1px solid $main-color;
        p {
            width: 335px;
            padding: 15px;
            height: fit-content; }
        .home-item {
            flex-direction: column;
            #alpaca {
                margin-bottom: 15px; }
            .skill-div {
                margin-top: 15px; } }
        #taei-img {
            width: 250px;
            margin-top: 10px;
            margin-bottom: 10px; } }

    .project-div {
        border: 1px solid $main-color;
        section {
            display: flex;
            flex-direction: column;
            img {
                width: 100%; }
            h4 {
                width: 250px;
                margin-left: 0px; }
            p {
                width: 250px;
                height: 350px;
                margin-left: 0px; } } }

    .contact-div {
        flex-direction: column;
        border: 1px solid $main-color;
        .id-card {
            width: 330px;
            height: 260px;
            margin-top: 15px;
            margin-bottom: 15px;
            span {
                transform: translate( 250px , -20px); } }
        .contact-card {
            width: 330px;
            h4 {
                margin-bottom: 15px; } } } }

@include tablet {
    .container-div {
        width: 90%;
        height: fit-content; }
    .container-div::after {
        display: none; }
    .home-div {
        flex-direction: column-reverse;
        overflow-y: scroll;
        .home-text {
            padding-bottom: 15px;
            margin-bottom: 15px;
            p {
                height: fit-content; } }
        #taei-img {
            width: 250px;
            margin-top: 10px;
            margin-bottom: 10px; } }

    .project-div {
        section {
            #piggys {
                width: 100%; }
            #cuting-img {
                width: 40%; }
            p {
                overflow-y: scroll; } } }
    .contact-div {
        flex-direction: column;
        .id-card {
            margin-top: 15px;
            margin-bottom: 18px; }
        .contact-card {
            margin-bottom: 20px; } } }
